	import {
        mapMutations,mapState
    } from 'vuex';
    //import {Decrypt,Encrypt} from '../../security/cryptojs'

	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false,

				form_query:{
                    telephone:"",
                    encrptPassword:""
				}
			}
		},
		onLoad(){

		},
        computed:{
            ...mapState(['hasLogin','userInfo'])
        },
		methods: {
			...mapMutations(['login','addressListSave']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
            navToRegister(){
			    var url = '/pages/public/register';
                uni.navigateTo({
					url
				})
            },
			navBack(){
				uni.navigateBack();
			},
			toRegist(){
				this.$api.msg('去注册');
			},
            navToHome(){
                var url = '/pages/index/index';
                console.log(url)
                uni.switchTab({
                    url
                })
            },
			getUsersDefaultAddress(){
                var params = this.userInfo.id
				console.log(params)
                this.$http.post("address/selectUserDefaultAddress?userId="+ params).then((res) => {
                    console.log("selectUserDefaultAddress")
                    console.log(res.data.data)
                    this.addressListSave(res.data.data)
                }).catch(error => {
                    console.log(error)
                }).finally(() => {
                    //console.log("success")
                })

			},
			async toLogin(){
                if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(this.form_query.telephone)){
                    this.$api.msg('请输入正确的手机号码');
                    return;
                }

                if(this.form_query.encrptPassword == null || this.form_query.encrptPassword == "" ){
                    this.$api.msg('请输入密码！');
                    return;
                }
				 //this.form_query.telephone = Encrypt(this.form_query.telephone)
				 //this.form_query.encrptPassword = Encrypt(this.form_query.encrptPassword)
				this.logining = true;
                var params = this.form_query
                this.$http.post("user/login", params).then((res) => {
                    console.log("success")
                    console.log(res)
                    if(res.data.status === "success"){
                        this.login(res.data.data);
                        var userId = res.data.data.id
						this.getUsersDefaultAddress()
                        // this.$http.get("user/selectUserDefaultAddress? userId="+userId).then((res) =>{
						// 	console.log(res.data.data)
						// 	this.addressListSave(res.data.data)
						// }
                        this.navToHome()
                    }else{
                        this.$api.msg(res.data.data.errMsg);
                        this.logining = false;
                        this.form_query = {}
                    }
                    // if (res.data.status == "fail") {
                    //     this.$api.msg(res.data.data.errMsg);
                    // }else {
                    //     // this.navToLogin()
                    // }
                }).catch(error => {
                    console.log(error)
                }).finally(() => {
                    //console.log("success")
                })
            },


		},

	}
